<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process226"></a>

<p><br></p>
<p><span class="rvts17">提倡者（</span><span class="rvts50">The Advocate</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:279,Y:353</span><span class="rvts21">，提倡者（</span><span class="rvts20">The Advocate</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">杀掉先祖之树下的黑环占领者</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1298.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">提倡者首先出现的位置是在血月岛的一处海滩上，与他谈话，他会让你帮助他杀掉先祖之树下的黑环占领者。如果同意帮助他，他将立马为我们提升</span><span class="rvts20">1</span><span class="rvts21">点的源力最大值，并且承诺如果我们成功，会告诉我们无名岛的位置。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1299.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">先祖之树下有</span><span class="rvts20">4</span><span class="rvts21">名黑环魔导师，并且可以召唤两只泥人与我们对抗。干掉这群黑环士兵后，提倡者会瞬间过来并感谢你后让你离开。</span>
<br><span class="rvts21">此时可以选择杀掉提倡者，完成</span><span class="rvts20"></span><span class="rvts21">邪恶之物的猎人（</span><span class="rvts20">A Hunter of Wicked Thing</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线的第一部分。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



